
.home-search {
    float: right;
    margin-top: -30px;
    color: #000;
    font-size: 12px;
}
.home-search input[type=text] {
    border: 1px solid #c1c3c8;
    height: 22px;
    line-height: 22px;
    width: 220px;
}
.home-search input[type=button] {
    width: 66px;
    height: 22px;
    color: #fff;
    font-size: 14px;
    background: #666e9f;
    border: 0;
    border-radius: 3px;
}
.home-search select {
    height: 22px;
    outline: none;
}
.detail-info {
    height: 134px;
    padding: 0 50px;
}
.detail-info .term-total {
    width: 66%;
    float: left;
    padding-top: 45px;
}
.term-total label {
    color: #838892;
    font-size: 18px;
    font-family: '微软雅黑';
}
.term-total em {
    font-style: normal;
    color: #565e69;
    font-size: 32px;
    font-family: 'Century Gothic', Arial, Helvetica, sans-serif;
    vertical-align: -2px;
    padding-left: 10px;
}
.term-total span {
    padding-left: 20px;
    color: #82878e;
    font-size: 20px;
    display: inline-block;
}
.detail-info .user-total {
    width: 33%;
    float: right;
    text-align: right;
    padding-top: 45px;
}
.user-total label {
    color: #838892;
    font-size: 18px;
    font-family: '微软雅黑';
}
.user-total em {
    font-style: normal;
    color: #565e69;
    font-size: 32px;
    font-family: 'Century Gothic', Arial, Helvetica, sans-serif;
    vertical-align: -2px;
    padding-left: 10px;
}
.user-total span {
    padding-left: 20px;
    color: #82878e;
    font-size: 20px;
    display: inline-block;
}
.chart {
    display: table;
    border-collapse: collapse;
    width: 100%;
}
.chart-row {
    display: table-row;
}
.chart-box {
    display: table-cell;
    width: 50%;
    border: 1px solid #e0e0e0;
    height: 248px;
    vertical-align: top;
}
.chart-content {
    width: 100%;
    height: 100%;
}
.chart-head {
    position: relative;
}
.chart-head h3 {
    color: #565e69;
    font-weight: normal;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
    position: absolute;
    left: 20px;
}
.chart-info {
    position: absolute;
    top: 30px;
    right: 25px;
    color: #82878e;
    font-size: 14px;
    z-index: 9;
}
.chart-info>div {
    display: inline-block;
}
.chart-info>select {
    margin-left: 15px;
    width: 94px;
    height: 22px;
    line-height: 22px;
    border: 1px solid #e0e0e0;
    background: #fafafa;
    color: #57606f;
}
.chart-info em {
    font-family: Arial;
    font-size: 20px;
    font-style: normal;
    color: #57606f;
    padding: 0 3px;
}
.chart-info span {
    color: #a6abb3;
    font-size: 14px;
    padding-left: 10px;
}
.org-list {
    margin: 0;
    padding: 0;
    padding-top: 40px;
    padding-left: 15px;
    padding-right: 15px;
    list-style-type: none;
}
.org-list li {
    height: 30px;
    line-height: 30px;
}
.org-list a {
    display: block;
    height: 30px;
    color: #82878e;
    text-decoration: none;
    padding: 0 5px;
}
.org-list a:hover {
    background: #f2f2f2;
}
.org-list span {
    display: inline-block;
    width: 170px;
}
.org-list em {
    font-style: normal;
    font-weight: normal;
}
.org-list strong {
    margin-right: 20px;
    font-weight: normal;
}
.org-list a:hover strong {
    color: #29bcef;
}



